import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../../@uisftech/common/constant/config";
import { ConsumptionLimitQueryAddBean } from "./bean/consumption-limit-query-add.bean";

@Component({
    selector: 'consumptionLimitQueryAdd',
    templateUrl: './consumption-limit-query-add.component.html',
    styleUrls: ['./consumption-limit-query-add.component.scss'],
    providers: [Commfunc, ConfirmationService]
})
export class ConsumptionLimitQueryAddComponent implements OnInit {
    bean:ConsumptionLimitQueryAddBean = new ConsumptionLimitQueryAddBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    singleAdjustmentTypeList:any = [//调整类型列表
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    singleAdjustmentTypeDispaly:boolean = false;//调整类型列表 必选提示 开关
    singleAdjustmentModeList:any = [//调整方式列表
        {label: "单户", value: "1"}, 
        {label: "批量", value: "2"}
    ];
    singleAdjustmentModeDispaly:boolean = false;//调整方式列表 必选提示 开关
    singleCardNoDispaly:boolean = true;//卡号输入框 单一 开关
    singleCardNoPromptDispaly:boolean = false;//卡号 必填提示 开关
    manyManagerDispaly:boolean = false;//经办人名称 批量 开关
    manyManagerPromptDispaly:boolean = false;//经办人名称 批量 必填提示 开关
    singleUserNameDispaly:boolean = true;//经办人名称 单一 开关
    singleUserNamePromptDispaly:boolean = false;//经办人名称 必填提示 开关
    manyManagerAgencyDispaly:boolean = false;//经办机构 批量 开关
    manyManagerAgencyPromptDispaly:boolean = false;//经办机构 批量 必填提示 开关
    manyManagerAgencyList:any = [//批量 经办机构列表
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    singleAdjustmentConsumptionLimitDispaly:boolean = true;//当前消费额度 单一 开关
    singleAdjustmentConsumptionLimitPromptDispaly:boolean = false;//当前消费额度 必填提示 开关
    manyManagerDateDispaly:boolean = false;//经办日期 批量 开关
    manyManagerDatePromptDispaly:boolean = false;//经办日期 批量 必填提示 开关
    singleAdjustmentConsumptionLimitTypePromptDispaly:boolean = false;//消费透支额度类型 单一 必填提示 开关
    singleAdjustmentConsumptionLimitTypeList:any = [//消费透支额度类型列表 单一
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    singleAdjustmentDirectionPromptDispaly:boolean = false;//调整方向 单一 必填提示 开关
    singleAdjustmentDirectionList:any = [//调整方向列表 单一
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    singleAdjustmentConsumptionLimitMoneyPromptDispaly:boolean = false;//消费限额调整金额 单一 必填提示 开关
    singleAdjustmentConsumptionLimitMoneyList:any = [//消费限额调整金额列表 单一
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    

    threeRowDispaly:boolean = true;//开启第三行输入框
    fourRowDispaly:boolean = true;//开启第四行输入框

    singleOpeningAgencyDispaly:boolean = true;//开户机构 单一 开关
    singleOpeningAgencyList:any = [//开户机构列表 单一
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    singleOpeningAgencyPromptDispaly:boolean = false;//开户机构 单一 必选提示 开关
    singleManagerPromptDispaly:boolean = false;//经办人 单一 必填提示 开关
    singleManagerAgencyList:any = [//经办机构列表 单一
        {label: "请选择", value: ""}, 
        {label: "存款账户", value: "01"}, 
        {label: "贷款账户", value: "02"},
        {label: "信用卡账户", value: "03"}
    ];
    singleManagerAgencyPromptDispaly:boolean = false;//经办机构 单一 必填提示 开关
    singleManagerDatePromptDispaly:boolean = false;//经办日期 单一 必填提示 开关

    modeDispaly:boolean = false;//批量 模板下载与上传 开关
    tableDispaly:boolean = false;//批量 表格 开关

    singleSubDispaly:boolean = true;//单一 的提交 默认开启
    manySubDispaly:boolean = false;//批量 的提交 默认开启
    
    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService
    ) {
        
    }

    ngOnInit() {
        this.bean.singleAdjustmentModeNo = "1";//打开 单户
    }

    targetChange(){
        if(this.bean.singleAdjustmentModeNo=="1"){
            this.singleCardNoDispaly = true;
            this.manyManagerDispaly =false;
            this.singleUserNameDispaly = true;
            this.manyManagerAgencyDispaly = false;
            this.singleAdjustmentConsumptionLimitDispaly = true;
            this.manyManagerDateDispaly = false;
            this.threeRowDispaly = true;
            this.fourRowDispaly = true;
            this.singleSubDispaly = true;
            this.manySubDispaly = false;
            this.modeDispaly = false;
            this.tableDispaly = false;
            this.singleOpeningAgencyDispaly = true;
        }
        if(this.bean.singleAdjustmentModeNo=="2"){
            this.singleCardNoDispaly = false;
            this.manyManagerDispaly =true;
            this.singleUserNameDispaly = false;
            this.manyManagerAgencyDispaly = true;
            this.singleAdjustmentConsumptionLimitDispaly = false;
            this.manyManagerDateDispaly = true;
            this.threeRowDispaly = false;
            this.fourRowDispaly = false;
            this.singleSubDispaly = false;
            this.manySubDispaly = true;
            this.modeDispaly = true;
            this.tableDispaly = true;
            this.singleOpeningAgencyDispaly = false;
        }
    }
}