import { ModuleWithProviders } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { ConsumptionLimitQueryComponent } from "./consumption-limit-query/consumption-limit-query.component";

export const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'consumption-limit-query',component: ConsumptionLimitQueryComponent},
        ]
    }

]

export const ConsumptionLimitRouting: ModuleWithProviders = RouterModule.forChild(routes);