
import { OnInit, Component } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { Message, ConfirmationService } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { ConsumptionLimitQueryBean } from "./bean/consumption-limit-query.bean";

@Component({
    selector: 'consumptionLimitQuery',
    templateUrl: './consumption-limit-query.component.html',
    styleUrls: ['./consumption-limit-query.component.scss'],
    providers: [Commfunc,ConfirmationService]
})
export class ConsumptionLimitQueryComponent implements OnInit {
    bean:ConsumptionLimitQueryBean = new ConsumptionLimitQueryBean();
    msgs: Message[];
    title:string;//弹出窗标题
    ch: any = CALENDAR_CN;
    total: string;//总页数
    first: number = 0;
    pageNum: number;//当前页数
    pageSize: number;//每页条数
    tableValueList: any = [{//表格内容
        "x1": "1",
        "x2": "1",
        "x3": "1",
        "x4": "1",
        "x5": "1",
        "x6": "1",
        "x7": "1"
    }];

    adjustmentTypeList:any = [];//调整类型列表
    adjustmentModeList:any = [];//调整方式列表
    openingAgencyList:any = [];//开户机构列表
    managerAgencyList:any = [];//经办机构列表
    Display:boolean = false;//弹出框

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService
    ) {
        
    }
    
    ngOnInit() {
        
    }

    //查询
    query(){
        this.pageNum = 1;
        this.pageSize = 10;
    }

    //翻页
    paginate(event){
        //每页显示的条数
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = (this.pageNum - 1) * this.pageSize;
        this.query();
    }

    //新增
    addDisplay:boolean = false;//新增内容的开关
    add() {
        this.title = "新增";
        this.Display = true;
        this.addDisplay = true;
        this.detailsDisplay = false;
        this.updataDisplay = false;
    }

    //详情
    detailsDisplay:boolean = false;//详情内容的开关
    toDetails(col){
        this.title = "详情";
        this.Display = true;
        this.addDisplay = false;
        this.detailsDisplay = true;
        this.updataDisplay = false;
    }

    // 编辑
    updataDisplay:boolean = false;//编辑内容的开关
    toEdit(col){
        this.title = "编辑";
        this.Display = true;
        this.addDisplay = false;
        this.detailsDisplay = false;
        this.updataDisplay = true;
    }

    // 删除
    toDelete(col){
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            header: '删除',
			accept: () => {
                
			}
		});
    }
}